.customize-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px 10%;
background-color: black;
  min-height: 80vh;
  gap: 40px;
}

.customize-content {
  flex: 1;
  text-align: left;
}

.customize-content h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
  margin-bottom: 20px;
}

.customize-content p {
  font-size: 1.1rem;
  color: rgb(245, 233, 233);
  line-height: 1.6;
  margin-bottom: 30px;
  font-weight: 300px;
}

.explore-btn {
  background-color:white;
  color:black;
  font-size: 1.1rem;
  padding: 15px 38px;
  border: none;
  border-radius: 20px 20px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
  text-decoration: none;
}

.explore-btn:hover {
  background-color: #1d1d70;
}

.customize-image {
  flex: 1;
  text-align: right;
}

.customize-image img {
  max-width: 100%;
  border-radius: 10px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
}
